'use client';

import { type ComponentProps } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { cn } from '@axiom-ui/react/lib';

export type NavLinkProps = ComponentProps<typeof Link> & {
  isActive?: boolean;
  extraClass?: string;
};

export function NavLink(props: NavLinkProps) {
  const pathname = usePathname();

  const isActive = props?.isActive || pathname === props.href;

  return (
    <Link
      className={cn(
        'rounded-md px-3 py-1 text-[14px] text-interactive-accent-default transition-colors hover:bg-inverse-interactive-accent-hovered hover:text-interactive-accent-hovered data-[active=true]:bg-inverse-interactive-accent-default',
        props?.extraClass,
      )}
      data-active={isActive}
      {...props}
    />
  );
}
