'use client';

import { usePathname } from 'next/navigation';
import { ChevronDown } from 'lucide-react';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuTrigger,
} from '@axiom-ui/react/dropdown-menu';
import { NavLink } from './nav-link';

interface ExpansibleMenuItemProps {
  label: string;
  subItems: {
    label: string;
    path: string;
  }[];
}

export function ExpansibleMenuItem({
  label,
  subItems,
}: ExpansibleMenuItemProps) {
  const pathname = usePathname();

  const isActive = subItems.some((option) => option.path === pathname);

  return (
    <DropdownMenu>
      <DropdownMenuTrigger
        className="flex items-center rounded-md px-3 py-1 text-[14px] text-interactive-accent-default transition-colors hover:bg-inverse-interactive-accent-hovered hover:text-interactive-accent-hovered data-[active=true]:bg-inverse-interactive-accent-default"
        data-active={isActive}
      >
        {label}
        <ChevronDown size={16} />
      </DropdownMenuTrigger>
      <DropdownMenuContent className="flex w-40 flex-col border-[1px] border-subtle bg-base-1">
        {subItems.map((option) => (
          <NavLink extraClass="p-3" href={option.path} key={option.path}>
            {option.label}
          </NavLink>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
