'use client';

import { type ComponentProps } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

type BottomTabLinkProps = ComponentProps<typeof Link> & {
  icon: JSX.Element;
  title: string;
};

export function BottomTabLink({ icon, title, ...props }: BottomTabLinkProps) {
  const pathname = usePathname();
  const isActive = pathname === props.href;

  return (
    <Link
      className="flex flex-1 flex-col items-center gap-1 rounded-md py-1 text-interactive-accent-default transition-colors hover:cursor-pointer hover:bg-surface-subtle-hovered data-[active=true]:bg-inverse-interactive-accent-default"
      data-active={isActive}
      {...props}
    >
      {icon}
      <p className="text-[12px]">{title}</p>
    </Link>
  );
}
