import { type ComponentProps } from 'react';
import Link from 'next/link';
import { cn } from '@axiom-ui/react/lib';

type ProfileDropdownItemProps = ComponentProps<typeof Link> & {
  extraClassName?: string;
  title: string;
  subTitle?: string;
  icon?: JSX.Element;
};

export function ProfileDropdownItem({
  extraClassName,
  icon,
  title,
  subTitle,
  ...props
}: ProfileDropdownItemProps) {
  return (
    <Link
      className={cn(
        'flex items-center gap-3 px-2 py-3 transition-colors hover:cursor-pointer hover:bg-surface-subtle-hovered',
        extraClassName,
      )}
      {...props}
    >
      {icon}
      <div>
        <p className="text-[14px]">{title}</p>
        {subTitle ? <p className="text-[12px]">{subTitle}</p> : null}
      </div>
    </Link>
  );
}
